import React, { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";

import { HandlerBtns, Loading, UserExecuted } from "~/components";
import { useAuth } from "~/store/auth";
import Update from "~/components/crud/Update";
import HeadingTitle from "~/components/headingTitle/HeadingTitle";
import httpRequest from "~/utils/httpRequest";
import { toast } from "react-toastify";

import styles from "~/styles/components/form.module.scss";

function DepartmentUpdate() {
  const { auth } = useAuth();
  const form = "update";
  // const [departmentName, setDepartmentName] = useState("");
  // const [description, setDescription] = useState("");
  const [data, setData] = useState({
    departmentName: "",
    description: "",
    modifyBy: auth?.user?.username,
  });
  const [isLoaded, setIsLoaded] = useState(false);
  const { id } = useParams();
  const navigate = useNavigate();

  // Get current info
  useEffect(() => {
    setIsLoaded(false);
    httpRequest
      .get(`department/${id}`)
      .then((result) => {
        const data = result?.data?.data;
        setData({
          ...data,
          departmentName: data.departmentName,
          description: data.description,
        });
        setIsLoaded(true);
      })
      .catch((error) => {
        console.log(error);
        setIsLoaded(true);
      });
  }, [id]);

  // handle change value
  const handleChange = (e) => {
    setData({
      ...data,
      [e.target.name]: e.target.value,
    });
  };

  // Handle update department
  const handleUpdateDepartment = (e) => {
    setIsLoaded(false);
    httpRequest
      .put(`department/edit/${id}`, data)
      .then((result) => {
        result && toast.success("Edit department successfully!");
        // result && navigate("../view");
        setIsLoaded(true);
      })
      .catch((error) => {
        console.log(error);
        setIsLoaded(true);
      });
  };
  return (
    <Update>
      {isLoaded ? (
        <form onSubmit={handleUpdateDepartment} className="form-group">
          <HeadingTitle title={"department"} form={form} />
          <div className={styles["form-body"]}>
            <div className="d-flex">
              <label htmlFor="department">Department</label>
              <input
                type="text"
                className="form-control"
                defaultValue={data.departmentName}
                onChange={handleChange}
                placeholder="Accountant Leader"
                name="departmentName"
              />
            </div>
            <div className="d-flex">
              <label htmlFor="description">Description</label>
              <textarea
                cols="30"
                rows="2"
                className="form-control"
                defaultValue={data.description}
                onChange={handleChange}
                name="description"
              ></textarea>
            </div>
            <UserExecuted type={form} />
            <HandlerBtns action={form} />
          </div>
        </form>
      ) : (
        <Loading />
      )}
    </Update>
  );
}

export default DepartmentUpdate;
